.button-mob-menu {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 260px;
  height: 48px;
  overflow: hidden;
  border: 2px solid $accent-color;
  border-radius: 2px;
  font-family: 'Montserrat';
  font-size: 14px;
  font-weight: 700;
  white-space: nowrap;
  text-transform: uppercase;
  line-height: 1.2;
  background-color: transparent;
  color: $accent-color;
  cursor: pointer;
  &:focus,
  &:hover {
    color: #fff;
    background-color: $accent-color;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  }
}
.button-heder-menu {
  width: 159px;
  height: 48px;
  overflow: hidden;
  border: 2px solid $accent-color;
  border-radius: 2px;
  font-family: 'Montserrat';
  font-size: 14px;
  white-space: nowrap;
  font-weight: 700;
  text-transform: uppercase;
  line-height: 1.2;
  letter-spacing: 0.12em;

  background-color: transparent;
  color: $accent-color;
  transition: 0.5s;
  cursor: pointer;

  &:focus,
  &:hover {
    color: $text-color;
    background-color: $accent-color;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  }
}
@media screen and (max-width: 767px) {
  .button-heder-menu {
    display: none;
  }
}
.btn-wrapper {
  position: relative;
  width: 260px;
  height: 48px;
  font-size: 14px;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
  border: none;
  border: 2px solid $accent-color;
  border-radius: 2px;
  background-color: $accent-color;
  color: #fff;

  transition: 0.5s;

  &:focus,
  &:hover {
    color: $accent-color;
    background-color: $primary-background-color;
    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
  }

  @media screen and (min-width: 1366px) {
    width: 354px;
    height: 70px;
    font-size: 18px;
  }
}

